@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Roboto:wght@400;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
}

.DisplayText{background-color:#E52307;width:100%;height:2rem;box-shadow:0px 4px 4px rgba(0,0,0,0.25);display:flex;align-items:center;justify-content:center}.DisplayText .text{color:#DCD0CF;font-size:1rem}@media screen and (min-width: 768px){.DisplayText{height:3rem}}

.drum-pad{width:40px;height:40px;background-color:#30181A;border-radius:5px;margin:10px 6px;display:flex;align-items:center;justify-content:center;transition:0.3s all ease-in-out;cursor:pointer;box-shadow:0px 4px 4px rgba(0,0,0,0.25)}.drum-pad .inner-text{color:#DCD0CF;font-size:1rem}.drum-pad:hover{background-color:#E52307}@media screen and (min-width: 768px){.drum-pad{width:50px;height:50px;border-radius:10px;margin:1rem 0.5rem}}

.DrumPadsWrapper{max-width:300px;display:flex;flex-wrap:wrap;align-items:center;justify-content:center}@media screen and (min-width: 768px){.DrumPadsWrapper{max-width:350px}}

.Button{display:flex;flex-direction:column;align-items:center;justify-content:center}.Button .btn-area{width:30px;height:15px;background-color:#30181A;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:20px;position:relative;cursor:pointer}.Button .btn-area .btn{content:"";display:block;width:15px;height:15px;background-color:#DCD0CF;border-radius:50%;box-shadow:inset 0px 4px 4px rgba(0,0,0,0.25);transition:0.3s all ease-in-out;position:absolute;top:0;left:0}.Button .btn-area .clicked{left:15px}.Button .title-btn{color:#DCD0CF;margin-top:5px}@media screen and (min-width: 768px){.Button .btn-area{width:50px;height:25px}.Button .btn-area .btn{width:25px;height:25px}.Button .btn-area .clicked{left:25px}}

.Box{width:100%;height:30%;display:flex;align-items:flex-start;justify-content:center}.Box p{font-size:.7rem;color:white;margin-top:1rem;z-index:1}.Box p .author{font-size:.7rem;font-weight:bold;text-decoration:none;color:white;cursor:pointer}.backgroungImage{background:center url(/drum-machine/static/media/pexels-wendy-wei.8973faac.jpg);background-size:cover;display:flex;position:relative}.backgroungImage .backgroundColor{width:100%;height:100%;position:absolute;top:0;left:0;opacity:0.5}@media screen and (min-width: 1280px){.Box{width:50%;height:100%}}

.Display{width:100%;height:70%;padding:1.5rem;background-color:#483D8B;display:flex;flex-direction:column}.Display .block{height:50%;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.Display .header{display:flex;justify-content:space-between}.Display .title{font-size:1.5rem;text-align:center;max-width:100px;margin:0.5rem 0;color:#fff}.Display .title .styled-title{font-size:1.5rem;font-family:'Lobster', cursive;color:#FF8C00}.Display .footer{color:#fff;text-align:center}.Display .footer p{font-size:0.5rem}@media screen and (min-width: 768px){.Display{padding:3rem}.Display .title{font-size:2.5rem;max-width:250px}.Display .title .styled-title{font-size:2.5rem}.Display .footer p{font-size:1rem}}@media screen and (min-width: 1280px){.Display{width:50%;height:100%}}

.App{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column-reverse;
}

.clicked{
  background-color: #E52307;
}

@media screen and (min-width: 1280px){
  .App{
    flex-direction: initial;
  }
}
